import axios from '@/utils/uniAxios.js'
export default {
	data() {
		return {
			form: {
				collect: [{
					time: '2019-04-06 11:37',
					shopName: '麦田圈官网旗舰店',
					state: 5,
				}],
			},
			order: {
				status: '',
				img: '',
				wuliu: '',
				pay: '',
				statusMsg: '',
				color: ''
			},
			statuss: '',
			show: false,
			marginBottom: '',
			checked: true,
			orderList: {
				name: '',
				phoneNumber: '',
				address: ''
			},
			ids: [],
			orderItemList: [],
			totalCount: '',
			payCode:{
				appId: "",
				nonceStr: "",
				package: "",
				paySign: "",
				signType: "MD5",
				timeStamp: ""
			}
		}
	},
	onLoad(option) {
		console.log(option)
		this.ids = JSON.parse(option.deleIds)
		if (uni.getStorageSync('addressMsg')) { //从地址跳转回来
			let orderAddress = JSON.parse(uni.getStorageSync('addressMsg'));
			// 地址更新
			this.orderList = {
				name: orderAddress.name,
				phoneNumber: orderAddress.phoneNumber,
				address: orderAddress.province + orderAddress.city + orderAddress.region + orderAddress.detailAddress,
			}
		}

		this.getOrder();

	},
	onShow() {
		uni.login({
			provider: 'weixin',
			success: (loginRes) => {
				let code = loginRes.code;	
				uni.setStorageSync('code', code)
			}
		})
		// this.getOrder();
	},
	methods: {
		// 数据初始化
		getOrder() {
			let obj = {
				cartItemIds: this.ids
			}
			axios.post('/order/generateOrder', obj).then(res => {
				this.orderList = res.data.data.order;
				this.orderItemList = res.data.data.orderItemList;
				if (this.orderList.preferential) {
					this.totalCount = this.orderList.totalAmount - 1 - this.orderList.preferential;
					console.log(11111)
				} else {
					this.totalCount = this.orderList.totalAmount - 1
					console.log(5555)
				}

			});

		},
		// 修改地址
		toAddress() {
			uni.navigateTo({
				url: '../set/address?postOrder=1&ids='+JSON.stringify(this.ids)
			})
		},
		//使用积分
		swithChange({
			detail
		}) {
			// 计算实付金额
			this.checked = detail;
			if (this.checked) { //true，表示抵扣
				if (this.orderList.preferential) {
					this.totalCount = this.orderList.totalAmount - 1 - this.orderList.preferential
				} else {
					this.totalCount = this.orderList.totalAmount - 1
				}

			} else {
				if (this.orderList.preferential) {
					this.totalCount = this.orderList.totalAmount - this.orderList.preferential
				} else {
					this.totalCount = this.orderList.totalAmount 
				}
			}
		},
		// 立即支付
		payBtn() {
			let code = uni.getStorageSync('code')
			let obj = {
				code: code, //code
				orderSn: this.orderList.orderSn, //订单编号orderSn
				payType: 3, //支付类型
				rechargeMoney: 0.01, //支付金额s
				userId: 16 //用户id
			}
			let _this = this;
			axios.post('/pay/payOrder',obj).then(res=>{
				console.log(res)
				_this.payCode={
					appId: res.data.data.appId,
					nonceStr: res.data.data.nonceStr,
					package: res.data.data.package,
					paySign: res.data.data.paySign,
					signType: "MD5",
					timeStamp:res.data.data.timeStamp,
				}
				uni.requestPayment({
				    provider: 'wxpay',
				    timeStamp: _this.payCode.timeStamp,
				    nonceStr: _this.payCode.nonceStr,
				    package: _this.payCode.package,
				    signType: 'MD5',
				    paySign: _this.payCode.paySign,
				    success: function (res) {
				        console.log('success:' + JSON.stringify(res));
						uni.reLaunch({
							url: 'paySuccess?totalCount='+_this.totalCount+'&id='+_this.orderList.id
						})
				    },
				    fail: function (err) {
				        console.log('fail:' + JSON.stringify(err));
				    }
				})
			})
		}
	}
}
